// import React from 'react';
import { useState, useEffect } from 'react';
import { useParams } from "react-router-dom"
import fileDownload from 'js-file-download';
import { sentenceCase } from 'change-case';
import FileSaver, { saveAs } from 'file-saver';
import {
    MDBCol,
    MDBContainer,
    MDBRow,
    MDBCard,
    MDBCardText,
    MDBCardBody,
    MDBCardImage,
    MDBBtn,
    MDBBreadcrumb,
    MDBBreadcrumbItem,
    MDBProgress,
    MDBProgressBar,
    MDBIcon,
    MDBListGroup,
    MDBListGroupItem,
    MDBTable,
    MDBTableHead,
    MDBTableBody
} from 'mdb-react-ui-kit';
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Container,
    Stack,
    Grid,
    Typography
} from "@mui/material"
import { useForm } from "react-hook-form";
import Box from '@mui/material/Box';
import SendIcon from '@mui/icons-material/Send';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';

import { fontWeight } from '@mui/system';
import axios from 'axios';
import Popup from '../../components/Popup';

import Label from '../../components/label';
import NewUpload from './NewUpload';
import { variables } from "../../variables";

import Notification from '../../components/Notifications';
import NewRecommendation from './NewRecommendation';
import Caupdate from './Caupdate';
import { useAuthContext } from '../../auth/useAuthContext';




export default function Evalute(props) {
    const { user } = useAuthContext();
    const params = useParams()
    const userId = params.userId

    const [values, setValues] = useState([]);
    const [option, setOption] = useState('');
    const [comments, setComments] = useState([]);
    const [value, setValue] = useState('')
    const [row, setRows] = useState([]);
    const [file, setFiles] = useState('')
    const [uploaddocuments, SetUploadDocuments] = useState(null)
    const [openUpload, setUpload] = useState(false)
    const [openRecommend, setRecommend] = useState(false)
    const [recommendation, SetRecommendation] = useState(null)
    const [openPopup, setOpenPopup] = useState(false)
    const [comment, setComment] = useState(null)
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onBlur"
    });
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })

    const registerOptions = {
        status: '',
    }
    // const [Titles, setTitles] = useState([]);



    const getApp = async () => {
        try {
            const Endpoint = `Voting/${userId}`
            const data = await axios.get(variables.API_URL + Endpoint);

            setRows(data.data)

        }
        catch (e) {
            console.log(e);
        }
    }






    useEffect(() => {

        getApp();

    }, []);




    function handleError(event) { }
    const label = { inputProps: { 'aria-label': 'Switch demo' } };
    return (
        <>
            <section style={{ backgroundColor: '#fafafa' }}>
                <MDBContainer className="py-5">

                    <MDBRow>
                       
                       <MDBTable bordered borderColor="primary" >
                <MDBTableHead>

                  <tr>
                    <td colSpan={7}>
                      <Box sx={{ fontWeight: 'bold', m: 1 }}> Credit Commitee Decisions </Box>
                    </td>
                  </tr>

                </MDBTableHead>
                <MDBTableBody>
                    {row.map((item, i) => (
                      <tr key={i}>
                        <th>{item.fullname}</th>
                        <td><Label color={(item.status === 'Approved' && 'success') || (item.status === 'Declined' && 'error')}>{sentenceCase(item.status)}</Label></td>
                      </tr>
                 
                       
                  
                    ))}
                </MDBTableBody>
              </MDBTable>
                    </MDBRow>
                </MDBContainer>
            </section>


            <Popup
                openPopup={openUpload}
                setUpload={setUpload}
                title="New Upload"
            >
                <NewUpload
                    uploaddocuments={uploaddocuments}
                    setUpload={setUpload}
                />


            </Popup>


            <Popup
                openPopup={openRecommend}
                setRecommend={setRecommend}
                title="Recommendation / Comment"
            >
                <NewRecommendation
                    recommendation={recommendation}
                    setRecommend={setRecommend}
                // insernewdata={insernewdata}
                />


            </Popup>

            <Popup
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
                title="Recommendation Credit Analysist"
            >


                <Caupdate
                    setOpenPopup={setOpenPopup}
                // insernewdata={updatecaStatus}
                />


            </Popup>



            <Notification
                notify={notify}
                setNotify={setNotify}
            />
        </>
    );
}