
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Typography
  } from "@mui/material"

import { color } from "@mui/system";
import TextareaAutosize from '@mui/base/TextareaAutosize';
  import React, { useState, useEffect } from "react";
  import { useForm } from "react-hook-form";
  import axios from "axios";
  import Notifications from "../../components/Notifications";
  
  
  export default function Caupdate(props) {
    const {setOpenPopup,insernewdata} = props
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onBlur"
    });

    const handleCancel = () => {
        return setOpenPopup(false)
      }
      
      const registerOptions = {
        recommendation: { required: "Recommendation is required" },
        status: { required: "status is required" },
        }
    
        function handleRegistration(values) {
          insernewdata(values)
      }
     
      function handleError(event) { }
      const label = { inputProps: { 'aria-label': 'Switch demo' } };
  return (
    <Container maxWidth="lg" sx={{ margin: 0 }}>
   
  
   <TextareaAutosize
      aria-label="minimum height"
      minRows={3}
      placeholder="Recommendation / Comment"
      style={{ width: 400, height:150}}
      name="recommendation"
      id="recommendation"
      {...register('recommendation', registerOptions.recommendation)}
    />
<Typography sx={{color:'red'}}>{errors.recommendation?.type === "required" && "Recommendation is required"}</Typography>
   <br/>
      <Box flex={2}>
        <FormControl sx={{ width: '170px' }}>
          <InputLabel id="possitionId-label">Select status</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="status"
            label="status"
            sx={{ width: '238px' }}
            {...register('status')}
          >

            <MenuItem value="Recommend">Recommend</MenuItem>
            <MenuItem value="Released">Released</MenuItem>
          </Select>
          <Typography sx={{ color: 'red' }}>{errors.status?.type === "required" && "Status is required"}</Typography>
        </FormControl>

      </Box>
   <Button variant="contained"
                        onClick={handleSubmit(handleRegistration, handleError)}
                    >Submit</Button>
                    <Button onClick={handleCancel}>Cancel</Button>
   <br/>
   <br/>
              
  </Container>
  );
  }
  
  