
import {
  Button,
  Switch,
  InputLabel,
  MenuItem,
  FormControl,
  Select,
  TextField,
  autocompleteClasses,
  Box,
  Container,
  Stack,
  Typography
} from "@mui/material"
import React, { useState, useEffect } from "react";
import axios from "axios";
import Notifications from "../../components/Notifications";

export default function NewUpload(props) {
const {uploaddocuments,setUpload} = props
const [image,setImage]=useState('')
const [appid, setApid]=useState()
const [filename, setFileName]=useState('')
const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
const newDate = new Date()

const handleImage =(e) =>
{
console.log(e.target.files)

setImage(e.target.files);

}
function handleId(e)
{
console.log(e.target.value)
setApid(e.target.value)

}
const handleName=(e)=>
{
  
console.log(e.target.value)

setFileName(e.target.value);

}
const handleCancel = () => {
  return setUpload(false)
}

const handleapi =(e)=>
{
  e.preventDefault();
  const formData = new FormData()

  for (let i = 0; i < image.length; i+=1) {
   
    formData.append(`files`, image[i])
    formData.append(`FileName`, filename)
    formData.append(`Applicationid`, uploaddocuments)
   
}
  axios.post('https://localhost:7015/api/Upload',formData).then((res)=>{
    setNotify({
      isOpen: true,
      message: 'Document Uploaded Sucessfully',
      type: 'success'
  }) 
  setUpload(false)
  })
}
return (
  <Container maxWidth="sm" sx={{ margin: 0 }}>
  <Box flex={2}>
     
 <TextField type="file" onChange={handleImage}/>
 <br/>
 <br/>
 <TextField
                id="value"
              
                label="Enter File Name"
               onChange={handleName}
            />
 
 <br/>
 <br/>
 <Button variant="contained" onClick={handleapi}>Upload</Button>
 
 <Button onClick={handleCancel}>Cancel</Button>


                  <Typography sx={{color:"red",padding:5}}>Only PDF Files Supported </Typography>
              </Box>
</Container>
);
}

