
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Box,
    Container,
    Stack,
    Typography
  } from "@mui/material"
  import { useForm } from "react-hook-form";
import { color } from "@mui/system";
import TextareaAutosize from '@mui/base/TextareaAutosize';
  import React, { useState, useEffect } from "react";
  import axios from "axios";
  import Notifications from "../../components/Notifications";
  
  
  export default function NewRecommendation(props) {
  const {recommendation,setRecommend,insernewdata} = props
  const [image,setImage]=useState('')
  const [appid, setApid]=useState()
  const [filename, setFileName]=useState('')
  const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })
  const { register, handleSubmit, formState: { errors } } = useForm({
    mode: "onBlur"
});
  const newDate = new Date()
  
  const handleImage =(e) =>
  {
  console.log(e.target.files)
  
  setImage(e.target.files);
  
  }
  function handleId(e)
  {
  console.log(e.target.value)
  setApid(e.target.value)
  
  }
  const handleName=(e)=>
  {
    
  console.log(e.target.value)
  
  setFileName(e.target.value);
  
  }
  const handleCancel = () => {
    return setRecommend(false)
  }
  
  const registerOptions = {
    recommendation: { required: "Recommendation is required" },
    }

    function handleRegistration(values) {
      insernewdata(values)
  }
 
  function handleError(event) { }
  const label = { inputProps: { 'aria-label': 'Switch demo' } };
 
  return (
    <Container maxWidth="lg" sx={{ margin: 0 }}>
   
  
   <TextareaAutosize
      aria-label="minimum height"
      minRows={3}
      placeholder="Recommendation / Comment"
      style={{ width: 400, height:150}}
      name="recommendation"
      id="recommendation"
      {...register('recommendation', registerOptions.recommendation)}
    />
<Typography sx={{color:'red'}}>{errors.recommendation?.type === "required" && "Recommendation is required"}</Typography>
   <br/>
   <br/>
   <Button variant="contained"
                        onClick={handleSubmit(handleRegistration, handleError)}
                    >Submit</Button>
                    <Button onClick={handleCancel}>Cancel</Button>
   <br/>
   <br/>
              
  </Container>
  );
  }
  
  